.sidebar {
    display: none;
   
}

.sidebar.open {
    display: flex;
    flex-direction: column;
   text-align: center;
    gap: 63px;
     position: fixed;
    width: 317px;
    height: 500px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
}

.background {
    position: absolute;
    top: 0;
    left: 0;
    background-color:white;
    border-color: red;
    width: 317px;
    height: 500px;

 z-index: 1;
 
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
  
}

.nav a {
text-decoration: none;
color: #000;
font-size: 22px;
font-style: normal;
font-weight: 600;
line-height: normal;

}

.nav a:hover {
    color: #58ACFF;;
   
}

.nav a.active {
    text-decoration-line: underline;
    color: #0059B2;
}
.close {
    position: relative;
    border: none;
    background-color: white;
}

.close img {
    position: absolute;
    top: 16px;
    right: 24px;
  
}
